<template>
  <div class="edit">
    <div class="add-box" @keyup.enter="addData">
      <label for="name">名称：</label><input type="text" v-model.trim="name" class="input-box">
      <label for="inData">入库数量：</label><input type="text" v-model.number="inData" class="input-box">
      <button class="btn" :class="{'btn-add':allowAdd}" @click="addData">添加</button>
    </div>
    <h4 align="center">最近添加的水果</h4>
    <table class="list-table">
      <tr>
        <th>序号</th>
        <th>名称</th>
        <th>入库数量</th>
        <th>库存总量</th>
        <th>库存余量</th>
        <th>更新时间</th>
        <th>操作</th>
      </tr>
      <tr v-if="!listData.length">
        <td class="no-data" colspan="7">暂无数据</td>
      </tr>
      <tr v-if="listData.length" v-for="(item,index) in listData" :key="index">
        <td>{{index+1}}</td>
        <td>{{item.name}}</td>
        <td>{{item.inData}}</td>
        <td>{{item.stock}}</td>
        <td>{{item.balance}}</td>
        <td>{{item.update_time | formatDate}}</td>
        <td>
          <button class="btn btn-del" @click="setDelInfo(index, item.id)">删除</button>
        </td>
      </tr>
    </table>
    <my-modal :show.sync="showModal" :delIndex="delData.index" @confirm="delFruit"></my-modal>
  </div>
</template>
<script>
import MyModal from '@/components/MyModal';
import API from '@/api';
export default {
  name: "edit",
  data() {
    return {
      name: "",
      inData: "",
      listData: [],
      showModal: false,
      delData: {
        index: null,
        id: null
      }
    };
  },
  computed: {
    fomartNum() {
      return parseInt(this.inData) ? parseInt(this.inData) : 0
    },
    allowAdd() {
      return this.name && this.inData ? true : false
    }
  },
  methods: {
    addData() {
      if(!this.allowAdd) {
        return false
      }
      API.addFruit({
        name: this.name,
        in: this.inData,
        stock: this.inData,
        update_at: +new Date()
      }).then(res => {
        console.log(res);
        this.listData.push({
          id: res.result._id,
          name: res.result.name,
          inData: res.result.in,
          stock: res.result.stock,
          balance: res.result.balance,
          update_time: res.result.update_at
        })
        this.name = this.inData = ''
      })
    },
    setDelInfo(index, id) {
      this.delData.index = index
      this.delData.id = id
      this.showModal = true
    },
    delFruit(){
      this.showModal = false
      this.$store.dispatch('delFruitData',this.delData.id).then(res => {
        this.listData.splice(this.delIndex, 1)
        this.delData.index = null
        this.delData.id = null
      })
    }
  },
  components: {
    MyModal
  }
};
</script>
<style lang="less" scoped>
.input-box {
  margin: 0 15px 0 0;
}
//表格
.list-table {
  margin: 25px auto 15px;
  width: 100%;
  border-collapse: collapse;
  th {
    background: #bbbec4;
    font-size: 16px;
    text-align: center;
    line-height: 30px;
    color: #fff;
    border: 1px solid #bbbec4;
  }
  td {
    border: 1px solid #bbbec4;
    padding: 5px;
    text-align: center;
    font-size: 16px;
    color: #282b32;
  }
  .no-data{
    color: #DDDEE1;
    padding: 10px 0;
  }
}
</style>



